import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { FeatureVersion } from '@core/enums';
import { Ad } from '@core/models';
import { FiltersService } from '@feature/general/filters/services/filters.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-empty-ad-list',
    templateUrl: './empty-ad-list.component.html',
    styleUrls: ['./empty-ad-list.component.scss'],
    standalone: false
})
export class EmptyAdListComponent implements OnInit {
	constructor(
		private readonly filtersService: FiltersService,
		private readonly router: Router,
		private readonly route: ActivatedRoute
	) {}

	@Input() pageName: string;
	@Input() location?: string;
	@Input() section?: string;
	@Input() ads: Ad[] = [];

	readonly featureVersion = FeatureVersion;
	readonly buttonType = ButtonType;
	noAdsMessage: string;
	hasMultipleLocation = false;

	onPlaceAd(): void {
		this.router.navigate([APP_ROUTE.placeAd]);
	}

	getTranslationKey(): string {
		return this.pageName === 'seller' ? 'n3202' : 'n3849';
	}

	onReset(): void {
		this.filtersService.clearAllFilterControl();
	}

	ngOnInit(): void {
		const location = this.route.snapshot.queryParamMap.get('location');
		if (location) this.hasMultipleLocation = true;

		this.location = this.location?.split(',')[0];
		const locationSplit = this.location?.split('-');
		const formattedLocation = locationSplit?.map((part) => part.charAt(0).toUpperCase() + part.slice(1));
		this.location = formattedLocation?.join(' ');

		this.noAdsMessage = this.getTranslationKey();
	}
}
